.App {
  text-align: center;
}
html,body{
  background-color: #f0f2f5;
}
.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.noData{
  text-align: center;
  margin-top: 12%;
 
  
}
.noData  img{
  width: 100px;
}
.noData p{
  padding-top: 10px;
  color: #999;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
